// __tests__/hooks/useRouteActivityTracker.test.js
import { renderHook } from '@testing-library/react-native';
import useRouteActivityTracker from '../../src/hooks/useRouteActivityTracker';

jest.mock('expo-router', () => ({
  useSegments: jest.fn(() => []),
  usePathname: jest.fn(() => '/'),
}));

jest.mock('../../src/hooks/useActivityMetrics', () => ({
  __esModule: true,
  default: jest.fn(() => ({
    startSession: jest.fn(),
    endSession: jest.fn(),
    updateStreak: jest.fn(),
  })),
}));

describe('useRouteActivityTracker', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('Fonctions utilitaires extraites', () => {
    it('devrait dÃ©tecter correctement les exercices', () => {
      const mockStartSession = jest.fn();
      const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
      useActivityMetrics.mockReturnValue({
        startSession: mockStartSession,
        endSession: jest.fn(),
        updateStreak: jest.fn(),
      });

      const { useSegments } = require('expo-router');
      
      // Test avec diffÃ©rents types d'exercices
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      renderHook(() => useRouteActivityTracker());
      expect(mockStartSession).toHaveBeenCalledWith('VocabularyExercise');

      jest.clearAllMocks();
      
      useSegments.mockReturnValue(['level', 'GrammarExercise']);
      renderHook(() => useRouteActivityTracker());
      expect(mockStartSession).toHaveBeenCalledWith('GrammarExercise');

      jest.clearAllMocks();
      
      useSegments.mockReturnValue(['level', 'Assessment']);
      renderHook(() => useRouteActivityTracker());
      expect(mockStartSession).toHaveBeenCalledWith('Assessment');
    });

    it('devrait gÃ©rer les transitions entre exercices', () => {
      const mockStartSession = jest.fn();
      const mockEndSession = jest.fn();
      const mockUpdateStreak = jest.fn();
      
      const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
      useActivityMetrics.mockReturnValue({
        startSession: mockStartSession,
        endSession: mockEndSession,
        updateStreak: mockUpdateStreak,
      });

      const { useSegments, usePathname } = require('expo-router');
      
      // Premier exercice
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      usePathname.mockReturnValue('/level/VocabularyExercise');
      renderHook(() => useRouteActivityTracker());
      
      expect(mockStartSession).toHaveBeenCalledWith('VocabularyExercise');
      expect(mockStartSession).toHaveBeenCalledTimes(1);

      // Nettoyer les mocks pour le test suivant
      jest.clearAllMocks();
      
      // DeuxiÃ¨me exercice (nouveau hook, nouvelle instance)
      useSegments.mockReturnValue(['level', 'GrammarExercise']);
      usePathname.mockReturnValue('/level/GrammarExercise');
      renderHook(() => useRouteActivityTracker());
      
      expect(mockStartSession).toHaveBeenCalledWith('GrammarExercise');
      expect(mockStartSession).toHaveBeenCalledTimes(1);
    });

    it('devrait gÃ©rer les erreurs de session gracieusement', () => {
      const mockStartSession = jest.fn().mockImplementation(() => {
        throw new Error('Erreur de session');
      });
      const mockEndSession = jest.fn();
      const mockUpdateStreak = jest.fn();
      
      const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
      useActivityMetrics.mockReturnValue({
        startSession: mockStartSession,
        endSession: mockEndSession,
        updateStreak: mockUpdateStreak,
      });

      const { useSegments, usePathname } = require('expo-router');
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      usePathname.mockReturnValue('/level/VocabularyExercise');

      // Ne devrait pas planter malgrÃ© l'erreur
      expect(() => {
        renderHook(() => useRouteActivityTracker());
      }).not.toThrow();
    });

    it('devrait Ã©viter les re-dÃ©clenchements inutiles', () => {
      const mockStartSession = jest.fn();
      const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
      useActivityMetrics.mockReturnValue({
        startSession: mockStartSession,
        endSession: jest.fn(),
        updateStreak: jest.fn(),
      });

      const { useSegments, usePathname } = require('expo-router');
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      usePathname.mockReturnValue('/level/VocabularyExercise');

      // Premier rendu
      const hook = renderHook(() => useRouteActivityTracker());
      expect(mockStartSession).toHaveBeenCalledTimes(1);

      // MÃªme pathname, ne devrait pas redÃ©marrer (mÃªme instance)
      // Simuler un changement de segments mais mÃªme pathname
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      usePathname.mockReturnValue('/level/VocabularyExercise');
      
      // Forcer un re-render
      hook.rerender();
      
      // Devrait toujours Ãªtre 1 car mÃªme pathname
      expect(mockStartSession).toHaveBeenCalledTimes(1);
    });

    it('devrait nettoyer correctement lors du dÃ©montage', () => {
      const mockEndSession = jest.fn();
      const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
      useActivityMetrics.mockReturnValue({
        startSession: jest.fn(),
        endSession: mockEndSession,
        updateStreak: jest.fn(),
      });

      const { useSegments, usePathname } = require('expo-router');
      useSegments.mockReturnValue(['level', 'VocabularyExercise']);
      usePathname.mockReturnValue('/level/VocabularyExercise');

      // DÃ©marrer une session
      const hook = renderHook(() => useRouteActivityTracker());
      
      // Debug: VÃ©rifier que startSession a Ã©tÃ© appelÃ©
      console.log('ðŸ” DEBUG: Hook rendu, mockEndSession calls:', mockEndSession.mock.calls.length);
      
      // VÃ©rifier que startSession a Ã©tÃ© appelÃ©
      expect(mockEndSession).not.toHaveBeenCalled();
      
      // Simuler le dÃ©montage
      console.log('ðŸ” DEBUG: Avant unmount, mockEndSession calls:', mockEndSession.mock.calls.length);
      hook.unmount();
      console.log('ðŸ” DEBUG: AprÃ¨s unmount, mockEndSession calls:', mockEndSession.mock.calls.length);
      
      // VÃ©rifier que endSession a Ã©tÃ© appelÃ©
      expect(mockEndSession).toHaveBeenCalled();
    });
  });

  it('devrait s\'initialiser sans erreur', () => {
    const { useSegments, usePathname } = require('expo-router');
    useSegments.mockReturnValue(['home']);
    usePathname.mockReturnValue('/home');

    expect(() => {
      renderHook(() => useRouteActivityTracker());
    }).not.toThrow();
  });

  it('devrait dÃ©tecter un exercice de vocabulaire', () => {
    const mockStartSession = jest.fn();
    const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
    useActivityMetrics.mockReturnValue({
      startSession: mockStartSession,
      endSession: jest.fn(),
      updateStreak: jest.fn(),
    });

    const { useSegments, usePathname } = require('expo-router');
    useSegments.mockReturnValue(['level', 'VocabularyExercise']);
    usePathname.mockReturnValue('/level/VocabularyExercise');

    renderHook(() => useRouteActivityTracker());

    expect(mockStartSession).toHaveBeenCalledWith('VocabularyExercise');
  });

  it('devrait dÃ©tecter un assessment', () => {
    const mockStartSession = jest.fn();
    const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
    useActivityMetrics.mockReturnValue({
      startSession: mockStartSession,
      endSession: jest.fn(),
      updateStreak: jest.fn(),
    });

    const { useSegments, usePathname } = require('expo-router');
    useSegments.mockReturnValue(['level', 'Assessment']);
    usePathname.mockReturnValue('/level/Assessment');

    renderHook(() => useRouteActivityTracker());

    expect(mockStartSession).toHaveBeenCalledWith('Assessment');
  });

  it('ne devrait pas dÃ©marrer de session pour une page normale', () => {
    const mockStartSession = jest.fn();
    const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
    useActivityMetrics.mockReturnValue({
      startSession: mockStartSession,
      endSession: jest.fn(),
      updateStreak: jest.fn(),
    });

    const { useSegments, usePathname } = require('expo-router');
    useSegments.mockReturnValue(['home', 'dashboard']);
    usePathname.mockReturnValue('/home/dashboard');

    renderHook(() => useRouteActivityTracker());

    expect(mockStartSession).not.toHaveBeenCalled();
  });

  it('devrait gÃ©rer les segments vides', () => {
    const mockStartSession = jest.fn();
    const useActivityMetrics = require('../../src/hooks/useActivityMetrics').default;
    useActivityMetrics.mockReturnValue({
      startSession: mockStartSession,
      endSession: jest.fn(),
      updateStreak: jest.fn(),
    });

    const { useSegments, usePathname } = require('expo-router');
    useSegments.mockReturnValue([]);
    usePathname.mockReturnValue('/');

    expect(() => {
      renderHook(() => useRouteActivityTracker());
    }).not.toThrow();

    expect(mockStartSession).not.toHaveBeenCalled();
  });
});